import React, { useContext } from "react";
import MovieCard from '../components/MovieCard/index.js';
import { LoadingOutlined } from '@ant-design/icons';
import AuthContext from "../context/auth-context";
import classes from '../pages/DetailsMovie.module.css';

const DetailsMovie = () => {

    const context = useContext(AuthContext);

    return (
        <section id="details-movie" className={classes.detailsMovie} >
            {context.loading && <p >Loading...</p>}
            {!context.loading ? (
                context.movie && <MovieCard />
            ) : (
                <LoadingOutlined style={{ fontSize: '36px', color: '#00e36a' }} />
            )}
        </section>
    )
}

export default DetailsMovie;